import React, { useState, useEffect } from 'react';
import './css/FooterImage.css';
import { isElementOfType } from 'react-dom/test-utils';

function FooterImage() {
  const [animationOverd, setAnimationOverd] = useState(0);

  const images = [
    "footer-city3-1.png",
    "footer-city3-2.png",
    "footer-city3-3.png",
    "footer-city3-4.png",
    "footer-city3-5.png",
    "footer-city3-6.png"
  ];

  const imageItems = images.map((item,index) => 
    <>
      <img key={item.toString()} src={`${process.env.PUBLIC_URL}/img/${item}`} alt="" className={animationOverd >= index + 1 ? "footer-image active" : "footer-image"} />
    </>
  );

  const footerEvent = () => {
    const promise = new Promise((resolve) => {
      setTimeout(() => {
        resolve(setAnimationOverd(1));
      },250)
    })
    promise.then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(2));
        },250)
      })
    }).then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(3));
        },250)
      })
    }).then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(4));
        },250)
      })
    }).then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(5));
        },250)
      })
    }).then((resolve) => {
        setTimeout(() => {
          setAnimationOverd(6);
        },250)
    })
  }

  const callFooterEvent = () => {
    if(animationOverd === 0){
      footerEvent();
    }
  }

  const [footerFixed, setFooterFixed] = useState(0);

  const scrolledEvent = () => {
    const domHeight = document.documentElement.scrollHeight;
    const footerHeight = document.getElementById('footerText').clientHeight;
    const scrollTop = document.documentElement.scrollTop;
    const viewHeight = document.documentElement.clientHeight;
    let scrollBottom;
    scrollBottom = scrollTop + viewHeight;
    if(scrollBottom > domHeight - footerHeight){
      setFooterFixed(footerHeight - (domHeight - scrollBottom))
    } else {
      setFooterFixed(0);
    }
  }

  useEffect(() => {
    window.addEventListener('scroll',scrolledEvent);
    return(() => {
      window.removeEventListener('scroll',scrolledEvent);
    })
  },[footerFixed])

  return(
    <>
      <div className="footer-images" onLoad={callFooterEvent()} style={{bottom: footerFixed}}>
        {imageItems}
      </div>
    </>
  )
}

export default FooterImage;