import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './css/HeaderNav.css';

function HeaderNav() {
  const [currentPage, setCurrentPage] = useState(0);

  const links = [
    "/",
    "/aboutme",
    "/charge",
    "/chirashi",
    "/contact",
    "/question",
    "/school"
  ];
  const images = [
    "icon-home.png",
    "icon-aboutme.png",
    "icon-charge.png",
    "icon-chirashi.png",
    "icon-contact.png",
    "icon-question.png",
    "icon-school.png"
  ];
  const texts = [
    "home",
    "aboutme",
    "charge",
    "chirashi",
    "contact",
    "question",
    "school"
  ];

  const scrollEvent = () => {
    if(window.scrollY > 0){
      window.scrollTo(0, 0)
    }
  }

  const reloadEvents = () => {
    for(let i = 0; i < links.length; i++){
      if(window.location.hash === `#${links[i]}`){
        setCurrentPage(i)
      }
    }
  }

  const headerTopEvents = () => {
    scrollEvent();
    setCurrentPage(0);
  }

  useEffect(() => {
    window.onpopstate = reloadEvents();
  },[])

  useEffect(() => {
    scrollEvent();
    document.getElementById('current').addEventListener('click',scrollEvent);
    document.getElementById('headerLink').addEventListener('click',headerTopEvents);
    return(() => {
      document.getElementById('current').removeEventListener('click',scrollEvent);
      document.getElementById('headerLink').removeEventListener('click',headerTopEvents);
    })
  },[currentPage])

  const linkItems = links.map((item,index) => 
    <>
      <li key={item.toString()} className={currentPage === index ? "nav-item current" : "nav-item"}>
        <Link to={item} onClick={() => setCurrentPage(index)} id={currentPage === index ? "current" : ""}>
          <div>
            <img src={`${process.env.PUBLIC_URL}/img/${images[index]}`} />
            <h1>{texts[index]}</h1>
          </div>
        </Link>
      </li>
    </>
  )

  return(
    <>
      <ul className="header-nav">
        {linkItems}
      </ul>
    </>
  )
}

export default HeaderNav;